        </div>
        <div class="status-item">
          <span class="label">TelegramÊîØÊåÅ:</span>
          <el-tag type="success">Â∑≤ÂêØÁî®</el-tag>
        </div>
          </el-col>
          <el-col :span="8">
            <div class="status-item">
              <span class="label">ËøûÊé•Áä∂ÊÄÅ:</span>
