                {{ isConnected ? 'Â∑≤ËøûÊé•' : 'Êú™ËøûÊé•' }}
              </el-tag>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="status-item">
              <span class="label">ÊúÄÂêéÊ¥ªÂä®:</span>
              <span class="value">{{ lastActivity || 'Êó†' }}</span>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-card>

    <!-- Ê∂àÊÅØÂèëÈÄÅÂå∫Âüü -->
    <el-card class="message-sender" shadow="never">
      <template #header>
        <div class="card-header">
          <span>üì§ ÂèëÈÄÅÊµãËØïÊ∂àÊÅØ</span>
          <el-tag type="info">ÊîØÊåÅTelegramÁæ§ÁªÑÊ∂àÊÅØ</el-tag>
        </div>
      </template>
      
      <el-form :model="messageForm" label-width="100px">
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="Áæ§ÁªÑID">
              <el-input v-model="messageForm.groupId" placeholder="ËæìÂÖ•Áæ§ÁªÑID" />
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="Áî®Êà∑ID">
              <el-input v-model="messageForm.userId" placeholder="ËæìÂÖ•Áî®Êà∑ID" />
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="Ê∂àÊÅØÁ±ªÂûã">
              <el-select v-model="messageForm.type" placeholder="ÈÄâÊã©Ê∂àÊÅØÁ±ªÂûã">
                <el-option label="TelegramÊ∂àÊÅØ" value="telegram_message" />
                <el-option label="Áæ§Ê∂àÊÅØ" value="group_message" />
                <el-option label="Ping" value="ping" />
                <el-option label="Ëé∑ÂèñÁä∂ÊÄÅ" value="get_status" />
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        
        <el-form-item label="Ê∂àÊÅØÂÜÖÂÆπ">
          <el-input 
            v-model="messageForm.message" 
            type="textarea" 
            :rows="3"
            placeholder="ËæìÂÖ•Ê∂àÊÅØÂÜÖÂÆπÔºåÊîØÊåÅTelegramÊú∫Âô®‰∫∫ÂëΩ‰ª§Â¶Ç: /Â∏ÆÂä©, /È¢Ñ‰ªò+100, /‰∏ãÂèë-50, /Êü•‰ΩôÈ¢ù, /Êü•ÊàêÁéá1"
          />
        </el-form-item>
        
        <!-- TelegramÊ∂àÊÅØ‰∏ìÁî®Â≠óÊÆµ -->
        <div v-if="messageForm.type === 'telegram_message'" class="telegram-fields">
          <el-form-item label="TelegramÊõ¥Êñ∞Êï∞ÊçÆ">
            <el-input 
              v-model="messageForm.telegramUpdate" 
              type="textarea" 
              :rows="4"
              placeholder="ËæìÂÖ•Telegram WebhookÊõ¥Êñ∞Êï∞ÊçÆ (JSONÊ†ºÂºè)"
            />
          </el-form-item>
        </div>
        
        <el-form-item>
          <el-button type="primary" @click="sendMessage" :disabled="!isConnected">
            ÂèëÈÄÅÊ∂àÊÅØ
          </el-button>
          <el-button @click="clearForm">Ê∏ÖÁ©∫Ë°®Âçï</el-button>
          <el-button type="success" @click="sendQuickCommand('help')">ÂèëÈÄÅÂ∏ÆÂä©ÂëΩ‰ª§</el-button>
          <el-button type="warning" @click="sendQuickCommand('ping')">ÂèëÈÄÅPing</el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <!-- Ê∂àÊÅØÊó•Âøó -->
    <el-card class="message-log" shadow="never">
      <template #header>
        <div class="card-header">
          <span>üìã Ê∂àÊÅØÊó•Âøó</span>
          <div>
            <el-button size="small" @click="clearLogs">Ê∏ÖÁ©∫Êó•Âøó</el-button>
            <el-button size="small" @click="toggleAutoScroll">
              {{ autoScroll ? 'ÂÖ≥Èó≠Ëá™Âä®ÊªöÂä®' : 'ÂºÄÂêØËá™Âä®ÊªöÂä®' }}
            </el-button>
          </div>
        </div>
      </template>
      
      <div class="log-container" ref="logContainer">
        <div 
          v-for="(log, index) in messageLogs" 
          :key="index"
          class="log-item"
          :class="log.type"
        >
          <div class="log-header">
            <span class="log-time">{{ log.timestamp }}</span>
            <el-tag :type="getLogTagType(log.type)" size="small">
              {{ getLogTypeText(log.type) }}
            </el-tag>
          </div>
          <div class="log-content">
            <pre>{{ log.content }}</pre>
          </div>
        </div>
        
        <div v-if="messageLogs.length === 0" class="empty-log">
          <el-empty description="ÊöÇÊó†Ê∂àÊÅØÊó•Âøó" />
        </div>
      </div>
    </el-card>

    <!-- ÁªüËÆ°‰ø°ÊÅØ -->
    <el-card class="statistics" shadow="never">
      <template #header>
        <div class="card-header">
          <span>üìä ÁªüËÆ°‰ø°ÊÅØ</span>
        </div>
      </template>
      
      <el-row :gutter="20">
        <el-col :span="6">
          <div class="stat-item">
            <div class="stat-value">{{ statistics.totalMessages }}</div>
            <div class="stat-label">ÊÄªÊ∂àÊÅØÊï∞</div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="stat-item">
            <div class="stat-value">{{ statistics.successMessages }}</div>
            <div class="stat-label">ÊàêÂäüÊ∂àÊÅØ</div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="stat-item">
            <div class="stat-value">{{ statistics.errorMessages }}</div>
            <div class="stat-label">ÈîôËØØÊ∂àÊÅØ</div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="stat-item">
            <div class="stat-value">{{ statistics.uptime }}</div>
            <div class="stat-label">ËøêË°åÊó∂Èó¥(Áßí)</div>
          </div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted, onUnmounted, nextTick } from 'vue'
import { ElMessage } from 'element-plus'

// WebSocketËøûÊé•
let ws: WebSocket | null = null
const isConnected = ref(false)
const lastActivity = ref('')
const wsUrl = 'ws://localhost:8789'

// Ê∂àÊÅØË°®Âçï
const messageForm = reactive({
  groupId: 'test_group_001',
  userId: 'test_user_001',
  type: 'telegram_message',
  message: '/Â∏ÆÂä©',
  telegramUpdate: JSON.stringify({
    update_id: 123456789,
    message: {
      message_id: 1,
      from: {
        id: 123456789,
        is_bot: false,
        first_name: 'Test',
        last_name: 'User',
        username: 'testuser'
      },
      chat: {
        id: -1001234567890,
        title: 'Test Group',
        type: 'supergroup'
      },
      date: Math.floor(Date.now() / 1000),
      text: '/Â∏ÆÂä©'
    }
  }, null, 2)
})

// Ê∂àÊÅØÊó•Âøó
const messageLogs = ref<Array<{
  timestamp: string
  type: string
  content: string
}>>([])

const logContainer = ref<HTMLElement>()
const autoScroll = ref(true)

// ÁªüËÆ°‰ø°ÊÅØ
const statistics = reactive({
  totalMessages: 0,
  successMessages: 0,
  errorMessages: 0,
  uptime: 0
})

// ËøûÊé•WebSocket
const connectWebSocket = () => {
  try {
    ws = new WebSocket(wsUrl)
    
    ws.onopen = () => {
      isConnected.value = true
      lastActivity.value = new Date().toLocaleString()
      addLog('success', 'WebSocketËøûÊé•Â∑≤Âª∫Á´ã')
      ElMessage.success('WebSocketËøûÊé•ÊàêÂäü')
    }
    
    ws.onmessage = (event) => {
      lastActivity.value = new Date().toLocaleString()
      handleMessage(event.data)
    }
    
    ws.onclose = () => {
      isConnected.value = false
      addLog('warning', 'WebSocketËøûÊé•Â∑≤Êñ≠ÂºÄ')
      ElMessage.warning('WebSocketËøûÊé•Êñ≠ÂºÄ')
    }
    
    ws.onerror = (error) => {
      isConnected.value = false
      addLog('error', 'WebSocketËøûÊé•ÈîôËØØ: ' + error)
      ElMessage.error('WebSocketËøûÊé•ÈîôËØØ')
    }
    
  } catch (error) {
    addLog('error', 'ËøûÊé•WebSocketÂ§±Ë¥•: ' + error)
    ElMessage.error('ËøûÊé•WebSocketÂ§±Ë¥•')
  }
}

// Êñ≠ÂºÄWebSocket
const disconnectWebSocket = () => {
  if (ws) {
    ws.close()
    ws = null
  }
}

// ÂàáÊç¢ËøûÊé•Áä∂ÊÄÅ
const toggleConnection = () => {
  if (isConnected.value) {
    disconnectWebSocket()
  } else {
    connectWebSocket()
  }
}

// Â§ÑÁêÜÊé•Êî∂Âà∞ÁöÑÊ∂àÊÅØ
const handleMessage = (data: string) => {
  try {
    const message = JSON.parse(data)
    addLog('info', JSON.stringify(message, null, 2))
    
    // Êõ¥Êñ∞ÁªüËÆ°‰ø°ÊÅØ
    statistics.totalMessages++
    if (message.type === 'telegram_message_result' || message.type === 'group_message_result') {
      if (message.success) {
        statistics.successMessages++
      } else {
        statistics.errorMessages++
      }
    }
    
  } catch (error) {
    addLog('error', 'Ëß£ÊûêÊ∂àÊÅØÂ§±Ë¥•: ' + error + '\nÂéüÂßãÊï∞ÊçÆ: ' + data)
  }
}

// ÂèëÈÄÅÊ∂àÊÅØ
const sendMessage = () => {
  if (!isConnected.value) {
    ElMessage.error('WebSocketÊú™ËøûÊé•')
    return
  }
  
  if (!messageForm.message.trim()) {
    ElMessage.error('ËØ∑ËæìÂÖ•Ê∂àÊÅØÂÜÖÂÆπ')
    return
  }
  
  const data = {
    type: messageForm.type,
    message: messageForm.message,
    group_id: messageForm.groupId,
    user_id: messageForm.userId
  }
  
  ws?.send(JSON.stringify(data))
  addLog('sent', 'ÂèëÈÄÅÊ∂àÊÅØ: ' + JSON.stringify(data, null, 2))
  ElMessage.success('Ê∂àÊÅØÂèëÈÄÅÊàêÂäü')
}

// ÂèëÈÄÅÂø´ÈÄüÂëΩ‰ª§
const sendQuickCommand = (command: string) => {
  if (!isConnected.value) {
    ElMessage.error('WebSocketÊú™ËøûÊé•')
    return
  }
  
  let data: any = {}
  
  switch (command) {
    case 'help':
      data = {
        type: 'group_message',
        message: '/Â∏ÆÂä©',
        group_id: messageForm.groupId,
        user_id: messageForm.userId
      }
      break
    case 'ping':
      data = { type: 'ping' }
      break
    case 'status':
      data = { type: 'get_status' }
      break
  }
  
  ws?.send(JSON.stringify(data))
  addLog('sent', 'ÂèëÈÄÅÂø´ÈÄüÂëΩ‰ª§: ' + JSON.stringify(data, null, 2))
  ElMessage.success('Âø´ÈÄüÂëΩ‰ª§ÂèëÈÄÅÊàêÂäü')
}

// Ê∑ªÂä†Êó•Âøó
const addLog = (type: string, content: string) => {
  messageLogs.value.push({
    timestamp: new Date().toLocaleString(),
    type,
    content
  })
  
  // ÈôêÂà∂Êó•ÂøóÊï∞Èáè
  if (messageLogs.value.length > 1000) {
    messageLogs.value = messageLogs.value.slice(-500)
  }
  
  // Ëá™Âä®ÊªöÂä®Âà∞Â∫ïÈÉ®
  if (autoScroll.value) {
    nextTick(() => {
      if (logContainer.value) {
        logContainer.value.scrollTop = logContainer.value.scrollHeight
      }
    })
  }
}

// Ê∏ÖÁ©∫Êó•Âøó
const clearLogs = () => {
  messageLogs.value = []
  ElMessage.success('Êó•ÂøóÂ∑≤Ê∏ÖÁ©∫')
}

// ÂàáÊç¢Ëá™Âä®ÊªöÂä®
const toggleAutoScroll = () => {
  autoScroll.value = !autoScroll.value
  ElMessage.info(autoScroll.value ? 'Â∑≤ÂºÄÂêØËá™Âä®ÊªöÂä®' : 'Â∑≤ÂÖ≥Èó≠Ëá™Âä®ÊªöÂä®')
}

// Ê∏ÖÁ©∫Ë°®Âçï
const clearForm = () => {
  messageForm.groupId = 'test_group_001'
  messageForm.userId = 'test_user_001'
  messageForm.type = 'group_message'
  messageForm.message = ''
}

// Ëé∑ÂèñÊó•ÂøóÊ†áÁ≠æÁ±ªÂûã
const getLogTagType = (type: string): 'primary' | 'success' | 'warning' | 'info' | 'danger' => {
  const typeMap: Record<string, 'primary' | 'success' | 'warning' | 'info' | 'danger'> = {
    'success': 'success',
    'error': 'danger',
    'warning': 'warning',
    'info': 'info',
    'sent': 'primary'
  }
  return typeMap[type] || 'info'
}

// Ëé∑ÂèñÊó•ÂøóÁ±ªÂûãÊñáÊú¨
const getLogTypeText = (type: string) => {
  const typeMap: Record<string, string> = {
    'success': 'ÊàêÂäü',
    'error': 'ÈîôËØØ',
    'warning': 'Ë≠¶Âëä',
    'info': '‰ø°ÊÅØ',
    'sent': 'ÂèëÈÄÅ'
  }
  return typeMap[type] || 'Êú™Áü•'
}

// ÂêØÂä®ÁªüËÆ°ÂÆöÊó∂Âô®
const startStatisticsTimer = () => {
  setInterval(() => {
    statistics.uptime++
  }, 1000)
}

// ÁªÑ‰ª∂ÊåÇËΩΩ
onMounted(() => {
  connectWebSocket()
  startStatisticsTimer()
})

// ÁªÑ‰ª∂Âç∏ËΩΩ
onUnmounted(() => {
  disconnectWebSocket()
})
</script>

